section:nth-of-type(2n+1) {
    background-color: #444;
    color: #fff
}

section:nth-of-type(2n) {
    background: rgb(250, 229, 142);
    background: linear-gradient(37deg,
            #ffea96 0%,
            #eed979 50%,
            #ebcb62 100%);
    color: #fff;
}

a {
    color: #000;
}

header {
    display: flex;
    justify-content: center;
    padding: 1.8em 2em;
    background: linear-gradient(45deg,
            #fae58e,
            #eccd64);
}

header>div {
    width: 1080px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* Logo typing */

@keyframes typing {
    from {
        width: 0
    }
}

@keyframes blink {
    50% {
        border-color: transparent;
    }
}

.logo {
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo a {
    width: 12ch;
    animation: typing 2s steps(40) infinite, blink .5s step-end infinite alternate;
    white-space: nowrap;
    overflow: hidden;
    border-right: 3px solid;
    font-family: monospace;
    font-size: 2em;
    color: #fff;
    text-decoration: none;
    font-weight: 700;
}

/* Nav */

nav ul {
    display: flex;
    column-gap: 1em;
    list-style: none;
    margin: 2em 0;
}

nav li {
    margin-bottom: .6em;
}

nav a {
    text-decoration: none;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 2px;
}

nav a:hover {
    border-bottom: 1px solid #fff;
}

.main-infos {
    width: 100%;
}

.main-infos>div {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5em 0;
    border-bottom: 2px solid #333;
}

/* Pulse back image */

.pulse {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    color: transparent;
    position: relative;
    animation: pulse 1.5s infinite;
    z-index: -1;
    margin-bottom: 10px;
}

.img-profile {
    width: 200px;
    height: 200px;
    border-radius: 50%;
}

.basic-infos {
    margin-top: 1em;
    text-align: center;
}

.basic-infos h1 {
    font-size: 1.6em;
}

.basic-infos h2 {
    padding-bottom: 10px;
}

.social-medias {
    font-size: 2em;
    display: flex;
    column-gap: .5em;
    background-color: #0002;
    padding: 8px 1em 0;
    margin: 20px 0;
    border-radius: .8em;
}

.social-medias a {
    transition: .8s;
}

.social-media:hover {
    transform: scale(1.2);
    transition: .2s;
}

.ring:hover {
    position: relative;
    animation: ring .2s infinite;
}

@keyframes pulse {
    0% {
        box-shadow: #eccd64 0 0 0 0;
    }

    75% {
        box-shadow: #fae58e00 0 0 0 20px;
    }
}

@keyframes ring {
    25% {
        transform: translateX(1px);
    }

    50% {
        transform: translateY(-1px);
    }

    75% {
        transform: translateX(1px);
    }

    100% {
        transform: translateY(-1px);
    }
}

.skills-wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2em;
    flex-wrap: wrap;
    column-gap: 2em;
    row-gap: 2em;
    color: #121212 !important;
}

.skills-wrapper>div {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2em;
    flex-wrap: wrap;
    column-gap: 2em;
    row-gap: 3em;
}

.skills-item {
    min-width: 320px;
    display: flex;
    flex-direction: column;
    row-gap: .2em;
    background-color: #fff3;
    padding: 2em 1em;
    border-radius: 10px;
    transition: .4s;
}

.skills-item ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: .7em;
}

.skills-item li p{
    text-align: center;
    font-size: 18px;
}

.skills-item:hover {
    transform: scale(1.05) !important;
    box-shadow: 0 10px 20px 0px #0004;
}

.skills-item h3 {
    text-align: center;
    font-size: 2em;
    margin-bottom: 1em;
}

.skill-xp {
    width: 280px;
    display: flex;
    column-gap: 10px;
    justify-content: space-between;
    margin: 0 auto;
}

.skill-name {
    display: flex;
    align-items: center;
    column-gap: .5em;
}

.skill-icon {
    font-size: 2em;
}

.stats-skills {
    display: flex;
    justify-content: center;
    align-items: center;
}

.amount-skill {
    width: 22px;
    height: 22px;
    border: 1px solid #121212;
    border-radius: 50%;
    display: grid;
    place-items: center;
}

.fill-skill {
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background-color: #555;
    animation: glow 2s infinite linear;
}

@keyframes glow {
    50% {
        opacity: .7;
    }
}

.next-section{
    display: grid;
    place-items: center;
    padding: 2em;
}

.next-section img {
    width: 80%;
}

@media screen and (max-width: 768px) {

    nav ul {
        flex-direction: column;
        margin: .5em 0;
    }

}

@media screen and (max-width: 481px) {
    .skills-item {
        transform: scale(1.05) !important;
        box-shadow: 0 10px 20px 0px #0004;
    }
}